.c-data-form {
  position: relative; }

.c-data-form__field {
  display: block;
  margin-bottom: .75rem; }

.c-data-form__label {
  margin-bottom: .375rem;
  display: block; }

.c-data-form__input {
  width: 100%; }

.c-data-form__submit-btn {
  width: 100%;
  margin-top: .75rem; }

.c-data-form__loading-spinner {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center; }

.c-form-layout__title {
  border-bottom: 1px solid #d1cdcd;
  display: block;
  margin-bottom: 1.75rem;
  padding-bottom: 0.5rem; }

.c-main-page-layout {
  max-width: 100%;
  width: 600px;
  margin: auto;
  padding: 2.5rem 1.25rem; }

.c-modal-layout {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 200px;
  background: #FFFFFF;
  padding: 2rem;
  box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.25); }

.c-get-authorized-backend-data__loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.c-full-list {
  position: relative; }

.c-full-list__item {
  margin-bottom: 1rem; }

.c-full-list__item-name {
  display: block; }

.c-full-list-item__options {
  display: flex; }

.c-full-list-item__edit {
  padding-left: 0; }

.c-delete-modal {
  position: relative; }

.c-delete-modal__btns {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 1rem; }

.c-delete-modal__spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.c-main-links__section {
  margin-bottom: 0.85rem;
  padding-bottom: 0.85rem;
  border-bottom: 1px solid #d1cdcd; }
  .c-main-links__section:last-child {
    border-bottom: none; }

.c-main-links__section-heading {
  margin-bottom: 0.2rem;
  display: block; }

.c-main-links__options {
  display: flex;
  align-items: center; }

.c-main-links__link {
  display: block; }
  .c-main-links__link:first-child {
    margin-right: 6px; }
  .c-main-links__link:last-child {
    margin-left: 6px; }
  .c-main-links__link:only-child {
    margin: 0; }

